        "id",
        "judul",
        "keterangan",
        "createdAt",
        "gambar1",
        "gambars",
        "video",
        "link",
        "tanggal",
        "type",
        "framework",
      ],
      vdata: {
          gambar1:'https://akbarmaliki.github.io/portofolio/img/',
          gambar2:'',
          gambar3:'',
          gambar4:'',
          gambar5:'',
          gambars:[],
          video:'',
          link:'',
      },
      ready: false,
      search: "",
      pilih: { name: "taufik", hobby: "coding" },
      thnya: {},
      err: false,
      checkedItem: [],
      less: ["_id",'id'],
      // modal
      modal: false,
      modalAction: "preview",
      // pagination
      selected: 10000,
      pagination: 1,
      temp1: 0,
      temp2: 10000,
      orderBy: "",
      orderWith: true,
      file: "",
      gambar2: "",
      imgs: [],
      //form
      aksi: "insert",
      formOn: false,
      updateOn: false,
    };
  },
  methods: {
    tambahGambar(){
      this.vdata.gambars.push(this.vdata.gambar2)
    },
      hapus(index){
          this.vdata.gambars.splice(index,1)
      },
    previewImage(event) {
      this.uploadValue = 0;
      this.picture = null;
      this.imageData = event.target.files[0];
    },
    previewImage1(event) {
      this.vdata.gambar1 = "https://akbarmaliki.github.io/portofolio/img/"+event.target.files[0].name
      this.$forceUpdate()
    },
    previewImage2(event) {
      this.vdata.gambar2 = "https://akbarmaliki.github.io/portofolio/img/"+event.target.files[0].name
      this.$forceUpdate()
    },
     onUploads() {
      this.picture = null;
      const storageRef = firebase
        .storage()
        .ref(`${this.imageData.name}`)
        .put(this.imageData);
      storageRef.on(
        `state_changed`,
        (snapshot) => {
          this.uploadValue =
            (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        },
        (error) => {
          console.log(error.message);
        },
        () => {
          this.uploadValue = 100;
          storageRef.snapshot.ref.getDownloadURL().then((url) => {
            this.picture = url;
            console.log("url", url);
            
            this.vdata.gambars.push(url)
            this.$forceUpdate()
          });
        }
      );
    },
    onUpload1(e) {
      console.log(e)
      // this.picture = null;
      // const storageRef = firebase
      //   .storage()
      //   .ref(`${this.imageData.name}`)
      //   .put(this.imageData);
      // storageRef.on(
      //   `state_changed`,
      //   (snapshot) => {
