      //   },
      //   () => {
      //     this.uploadValue = 100;
      //     storageRef.snapshot.ref.getDownloadURL().then((url) => {
      //       this.picture = url;
      //       console.log("url", url);
            
      //       this.vdata.gambar1 = url;
      //       this.$forceUpdate()
      //     });
      //   }
      // );
    },
    onUpload2() {
      this.picture = null;
      const storageRef = firebase
        .storage()
        .ref(`${this.imageData.name}`)
        .put(this.imageData);
      storageRef.on(
        `state_changed`,
        (snapshot) => {
          this.uploadValue =
