        },
        () => {
          this.uploadValue = 100;
          storageRef.snapshot.ref.getDownloadURL().then((url) => {
            this.picture = url;
            console.log("url", url);
            
            this.vdata.gambar4 = url;
            this.$forceUpdate()
          });
        }
      );
    },
    onUpload5() {
      this.picture = null;
      const storageRef = firebase
        .storage()
        .ref(`${this.imageData.name}`)
        .put(this.imageData);
      storageRef.on(
        `state_changed`,
        (snapshot) => {
          this.uploadValue =
            (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        },
        (error) => {
          console.log(error.message);
        },
        () => {
          this.uploadValue = 100;
          storageRef.snapshot.ref.getDownloadURL().then((url) => {
            this.picture = url;
            console.log("url", url);
            
            this.vdata.gambar5 = url;
            this.$forceUpdate()
          });
        }
      );
    },
    getFile(e) {
      this.file = e.target.files[0];
    },
    compress(event) {
      // this.imgList=e.target.files;
      if (event.target.files[0].size > 10000000) {
        alert("File yang di upload tidak sesuai kriteria");
      } else {
        if (event.target.files.length > 0) {
          let img = [];
          let hasil = [];
          let compress = "null";
          let filenya;
          for (let i = 0; i < event.target.files.length; i++) {
            this.imgs.push(event.target.files[i]);
            var selectedFile = event.target.files[i];
            var reader = new FileReader();
            reader.onload = function (event) {
              //   img.push(event.target.result)
            };
            reader.readAsDataURL(selectedFile);
            filenya = event.target.files[i];
            compress =
              filenya.size < 1000000
                ? 30
                : filenya.size < 2000000
                ? 25
                : filenya.size < 3000000
                ? 20
                : filenya.size < 4000000
                ? 15
                : 10;
            this.$daycaca.compress(filenya, compress, (data) => {
              this.gambar = data;
              img.push(data);
              this.$urltofile(data, filenya.name, filenya.type).then((res) => {
                hasil.push(res);
              });
            });
          }
          console.log("base64", img);
          setTimeout(() => {
            console.log("file", hasil[0]);
            this.file = hasil[0];
            this.ready = true;
          }, 1000);
          //   this.$emit('upload', hasil)
        }
      }
    },
    insert() {
      delete this.vdata.id;
      this.vdata.createdAt = new Date()
      db.collection(`tamaliki`)
        .doc()
        .set(this.vdata)
        .then((res) => {
          console.log("berhasil");
          alert("simpan data berhasil!");
        });
    },
    update() {
        console.log(this.vdata)
    //     this.vdata.gambar1= this.vdata.gambar1 == undefined ? '' : this.vdata.gambar1
    //     this.vdata.gambar2= this.vdata.gambar2 == undefined ? '' : this.vdata.gambar2
    //     this.vdata.gambar3= this.vdata.gambar3 == undefined ? '' : this.vdata.gambar3
    //     this.vdata.gambar4= this.vdata.gambar4 == undefined ? '' : this.vdata.gambar4
    //     this.vdata.gambar5= this.vdata.gambar5 == undefined ? '' : this.vdata.gambar5
    //     this.vdata.teknologi= this.vdata.teknologi == undefined ? '' : this.vdata.teknologi
    //     this.vdata.video= this.vdata.video == undefined ? '' : this.vdata.video
    //     this.vdata.link= this.vdata.link == undefined ? '' : this.vdata.link
      db.collection(`tamaliki`)
        .doc(this.pilih.id)
        .set(this.vdata, { merge: true })
        .then((res) => {
          alert("update data berhasil!");
        });
    },
    remove() {
      if (confirm("Apakah yakin menghapus data?")) {
        db.collection(`tamaliki`)
          .doc(this.pilih.id)
          .delete()
          .then((res) => {
            alert("delete data berhasil!");
          });
      }
    },
    refreshData() {
      let that = this;
      db.collection(`tamaliki`).onSnapshot((snap) => {
        let arr = [];
        snap.forEach((e) => {
          arr.push({ id: e.id, ...e.data() });
        });
        setTimeout(() => {
          (function () {
            var thElm;
            var startOffset;
            Array.prototype.forEach.call(
              document.querySelectorAll("table th"),
              function (th) {
                th.style.position = "relative";
                var grip = document.createElement("div");
                grip.innerHTML = "&nbsp;";
                grip.style.top = 0;
                grip.style.right = 0;
                grip.style.bottom = 0;
                grip.style.width = "5px";
                grip.style.position = "absolute";
                grip.style.cursor = "col-resize";
                grip.addEventListener("mousedown", function (e) {
                  thElm = th;
                  startOffset = th.offsetWidth - e.pageX;
                });
                th.appendChild(grip);
              }
            );
            document.addEventListener("mousemove", function (e) {
              if (thElm) {
                thElm.style.width = startOffset + e.pageX + "px";
              }
            });
            document.addEventListener("mouseup", function () {
              thElm = undefined;
            });
          })();
          let hasil = [];
          let obj = {};
          arr.forEach((e, index) => {
            that.order.forEach((e) => {
              obj[e] = arr[index][e];
            });
            hasil.push(obj);
            obj = {};
          });
          this.datanya = hasil;
          let keys = this.order;
          keys = this.$_.difference(keys, this.unless);
          this.thnya = keys;
        }, 1000);
      });
    },
    ambil(item, index) {
      window.scrollTo(0, 0);
      let that = this;
      this.updateOn = true;
      this.formOn = true;
      let clone = this.$_.clone(item);
      // clone["TGL_LHR"] = this.$datefns.format(
      //   new Date(clone["TGL_LHR"]),
      //   "YYYY-MM-dd"
      // );
      console.log(clone);
      this.vdata = clone;
      this.vdata.gambars = this.vdata.gambars ? this.vdata.gambars : []
      this.pilih = item;
    },
    clearInput() {
      this.formOn = true;
      let keys = Object.keys(this.vdata);
      keys.forEach((key) => {
        this.vdata[key] = "";
      });
    },

    //!==================
    //! hapus dari sini
    //! =================
    checkedAll() {
      console.log(this.checkedItem);
    },
    susun(datas) {
      let that = this;
      let data = datas;
      let order = [
        "id",
        "judul",
        "keterangan",
        "createdAt",
        "gambar1",
        "gambar2",
        "gambar3",
        "gambar4",
        "gambar5",
        "video",
        "link",
      ];
      let hasil = [];
      let obj = {};
      data.forEach((e, index) => {
        order.forEach((e) => {
          obj[e] = data[index][e];
        });
        hasil.push(obj);
        obj = {};
      });
      return hasil;
    },
    print1() {
      this.$router.push(`/laporan/perdata?less=${this.less.toString()}`);
      this.$store.state.print2 = [];
      this.$store.state.print2.push(this.pilih);
      this.$store.state.print2 = this.susun(this.$store.state.print2);
      alert("print satu");
    },
    print2() {
      this.$router.push(`/laporan/print?less=${this.less.toString()}`);
      let that = this;
      this.$store.state.print2 = this.susun(this.td);
      alert("print banyak");
    },
    linknya(hasil) {
      console.log("link", hasil);
      this.vdata.Gambars = hasil;
      this.$forceUpdate();
    },
    isDate(value) {
      var dateFormat;
      if (toString.call(value) === "[object Date]") {
        return true;
      }
      if (typeof value.replace === "function") {
        value.replace(/^s+|s+$/gm, "");
      }
      dateFormat = /(^d{1,4}[.|\/|-]d{1,2}[.|\/|-]d{1,4})(s*(?:0?[1-9]:[0-5]|1(?=[012])d:[0-5])ds*[ap]m)?$/;
      return dateFormat.test(value);
    },
    // MODAL
    format(date) {
      return this.$datefns.format(date, "DD MMMM YYYY", {
        locale: id,
      });
    },
    distanceToNow() {
      return this.$datefns.distanceInWordsToNow(
        new Date(2015, 0, 1, 0, 0, 15),
        {
          locale: id,
          includeSeconds: true,
        }
      );
      //=> "3 days ago"
    },
    // DATATABLE
    pagPrev() {
      this.pagination = this.pagination - 1;
      if (this.pagination == 0) {
        this.pagination = 1;
      }
      this.temp2 = this.pagination * this.selected;
      this.temp1 = Math.ceil(this.temp2) - this.selected;
    },
    pagNext() {
      this.pagination = this.pagination + 1;
      if (this.pagination > this.bagi + 1) {
        this.pagination = Math.round(this.bagi);
      }
      this.temp2 = this.pagination * this.selected;
      this.temp1 = Math.ceil(this.temp2) - this.selected;
    },
    sort(item) {
      this.orderBy = item;
      this.orderWith = !this.orderWith;
    },
    //!==================
    //! hapus sampai sini
    //! =================
  },
  props: {
    unless: {
      type: Array,
      required: false,
      default: () => ["gone"],
    },
    date: {
      type: Array,
      required: false,
      default: () => ["ditanyakan"],
    },
  },
  watch: {
    datanya() {
      if (this.datanya.length > 0) {
        this.ready = true;
      }
    },
    selected() {
      this.temp2 = this.pagination * this.selected;
      this.temp1 = this.temp2 - this.selected;
    },
    search() {
      this.pagination = 1;
      this.temp2 = this.pagination * this.selected;
      this.temp1 = this.temp2 - this.selected;
    },
  },
  computed: {
    // DATATABLE
    bagi() {
